body {
  color: #20262c;
  background: #fff;
  font-family: "Lato", Arial, sans-serif;
  font-size: 15px;
  line-height: 1.27;
}

body.is-hidden {
  overflow: hidden;
}

body.is-hidden:before {
  opacity: 1;
  visibility: visible;
}

.out {
  overflow: hidden;
}

.inner {
  position: relative;
  padding-left: 135px;
  padding-right: 135px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1440px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1439px) {
  .inner {
    padding-left: 70px;
    padding-right: 70px;
  }
}

@media only screen and (max-width: 1200px) {
  .inner {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media only screen and (max-width: 767px) {
  .inner {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.title-xl,
.title-lg {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 48px;
  font-weight: 300;
  line-height: 0.96;
  letter-spacing: 1.4px;
}

@media only screen and (max-width: 767px) {
  .title-xl {
    font-size: 30px;
    line-height: 1.07;
    letter-spacing: 0.9px;
  }
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 28px;
    line-height: 1.14;
    letter-spacing: 0.8px;
  }
}

.title-md {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 20px;
  font-weight: 300;
  line-height: 1;
  letter-spacing: 0.6px;
}

.title-sm {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: 0.5px;
}

.description {
  font-size: 15px;
  line-height: 1.33;
}

@media only screen and (max-width: 767px) {
  .description {
    font-size: 14px;
    line-height: 1.29;
  }
}

.description p + p {
  margin-top: 19px;
}
